:root {
    color-scheme: dark;
    --bg: #212121;
    --accent-bg: #2b2b2b;
    --text: #dcdcdc;
    --text-light: #ababab;
    --border: #666;
    --accent: #ffb300;
    --code: #f06292;
    --preformatted: #ccc;
    --disabled: #111;
}
/* Add a bit of transparency so light media isn't so glaring in dark mode */
img,
video {
opacity: 0.8;
}

body>header{
    padding-top: 5rem;
    border-bottom: 0;
    background: var(--bg);
}

header h1 a{
    border-radius: 50%;
    background-image: url(" http://localhost:4000/assets/duck.png");
    background-size: cover;
    height: 3em;
    width: 3em;
    display: inline-block;
    text-indent: -9000px;
}

h1{ text-align: center; }

main iframe {
    display: block;
    margin: 0 auto;
}
